
.skillData{
    background: var(--orange);
    height: 100%;
    border-radius: 10px;
}

.progressBox{
    display: inline-block;
    width: 100%;
    background: #c5c5c5;
    height: 10px;
    border-radius: 10px;
}
.progressBoxContainer{
    width: 45%;
    margin-right: 5%;
    margin-top: 30px;
}

@media only screen and (max-width: 768px) {
    
    .skillData{
        background: var(--orange);
        height: 100%;
        border-radius: 10px;
    }
    
    .progressBox{
        width: 100%;
      
    }
    .progressBoxContainer{
        width: 100%;
        
    }

}