.contactPage{
    /* background: var(--orange); */
    /* color: #fff; */
    height: 100vh;
    /* padding: 0 100px; */
}

.contactMe{ 
    display: flex; 
}
.contact{ 
    width: 50%;
    padding: 0 100px 
}
.contactDesc{
    font-size: 25px;
    line-height: 40px;
}

.contactInfo{
    display: flex;
    margin-top: 50px;
    justify-content: space-around;
    flex-wrap: nowrap;
}
.socialLinks{
    margin: 50px 0;
    display: flex;
    justify-content: space-evenly;
}
.contactForm{
    width: 50%;
    padding-right: 100px;
}
.formHeading{
    padding: 0;
}


@media only screen and (max-width: 768px) {
    .contactMe{
        flex-direction: column 

    }
    .contact{ 
        width: 100%;
        padding: 0; 
    }
    .contactDesc{ 
        font-size: 20px;      
        width: 100%;
        padding: 0 10%;
        text-align: center;
    }
    .contactInfo{

        justify-content: space-evenly;
    }

    .contactForm{
        width: 100%;

        padding: 20% 10%;
    }

    .formHeading{
        padding-bottom: 30px;
    }

}