.navClass {
  display: inline-block;
  color: #000;
  text-decoration: none;
  padding: 5px 30px;
  font-size: 20px;
}

  .navClass::after {
    content: '';
    display: block;
    margin: auto;
    width: 0;
    height: 5px;
    border-radius: 10px;
    background: var(--orange);
    transition: width .5s ease, background-color .5s ease;
  }
/* navClass:hover{
  border-bottom: 5px solid #ff0000;
} */
.navClass:hover::after {
    width: 100%;
    /* transition: width .3s; */
}
.active::after{
  content: '';
    display: block;
    margin: auto;
width: 100%;
    height: 5px;
    background: var(--orange);
}
  .inactive {
    color: black;
  }

  
  .navlink{
    padding: 5px 30px;
        margin: auto;
        /* border-radius: 4px; */
        color: #231232;
        /* background: #9a8faf; */
        text-decoration: none;
        font-size: 20px;
  }