.resumepage{
    /* position: absolute; */
    /* height: 100vh; */
    width: 100%;
    /* top: 325vh; */
    background: #fff;
}

.resumeContent{
    display: flex;
}
.eduContainer{
    width: 50%; 
    border-right: 5px solid var(--orange);
}
.expContainer{
    width: 50%;
}
.education{
    float: left;
    /* width: 40%; */
    /* border-right: 5px solid #f06f43; */
    height: 100%;
    padding: 0 100px;

}
.heading{
    padding: 30px 0;
    font-size: 28px;
}
.experiance{
    /* float: right; */
    padding: 0 100px;

}
.mySkills{
    padding: 0 100px;
}
.myCv{
    display: flex;
    justify-content: center;
    padding-bottom: 100px;
}
.oneSkill{
    display: flex;
    margin-bottom: 100px;
    flex-wrap: wrap;
}
@media only screen and (max-width: 768px) {

    .resumeContent{
        flex-direction: column;
        text-align: center;
    }

    .eduContainer{
        width: 100%; 
        border-right: none;
    }
    .expContainer{
        width: 100%; 
    }
    .experiance{
        padding: 0 50px;
    }

    .education{
        padding: 0 50px;
    }
    .degreeRole{ 
        padding-bottom: 10px;
        font-size: 21px;
     }
    .edesc{ 
        padding-bottom: 10px;
     }
     .mySkills{
        /* text-align: center; */
        padding: 0 50px;
    }
    .heading{
        text-align: center;
    }
    .oneSkill{
        flex-direction: column;

    }
}