.landing{
    height: 100vh;
    width: 100%;
    background:
    linear-gradient(
      rgba(0, 0, 0, 0.65),
      rgba(0, 0, 0, 0.65)
    ),
    url('../img/LandingBackground2.jpg');
    background-size: cover;
    /* position: absolute; */
    background-repeat: no-repeat;
    backdrop-filter: blur(1px);
    text-align: center;
    animation: fadIn 3s;
}


.fontAwesomeIcon {
    position: absolute;
    bottom: 10px;
  }

  