.intro{
  margin: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  color: #fff;
}
.branding{
  color: var(--orange);
}
.intro > h1 {
  font-size: 60px;
}

.intro > h2 {
  font-size: 40px;
}
.typingDemo {
  padding: 0;
  width: 21ch;
  animation: typing 3s steps(21) infinite, blink .3s step-end infinite alternate;
  white-space: nowrap;
  overflow: hidden;
  border-right: 3px solid;
  font-family: monospace;
  
}

@keyframes typing {
  from {
    width: 0
  }
}
    
@keyframes blink {
  50% {
    border-color: transparent
  }
}

@media only screen and (max-width: 768px) {
  .typingDemo {
    font-size: 30px;
    
  }  
}